@import url('https://fonts.googleapis.com/css2?family=Patrick+Hand&display=swap'); /* if you chose Patrick Hand */

* {
  margin: 0;
  padding: 0;
}

.main {
  width: 100%;
  height: 100vh;
}

video {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.content{
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: azure;
  font-family: 'Patrick Hand', cursive;
}